

*{
    margin: 0;
    padding: 0;
}
.container{
   position: relative;
   top:   0px;
  
    padding: 25px 38px;
    width: 350px;
    border: 1px solid silver;
    border-radius: 10px;
    overflow: hidden;
    background-color: var(--nav_background);
   margin-top: 20px;
    transition: all 0.2s ease;
    text-align: center;
}
.container:hover {
  box-shadow: var(--box-shadow)
}



.horizontal_scroll_wrapper{
  border-bottom: 2px dashed #559AAA;;
  width: 275px;
  height: 300px;
  overflow-x: scroll;
  // overflow-y: hidden;
  // height: 80px;
  white-space:nowrap;
  // overflow-y: auto;
  // overflow-x: hidden;
}

