.title__container{
    display: flex;
    justify-content: space-between;
}
    
.container h3{
width: 81px;
height: 45px;
font-family: 'Overpass';
font-style: normal;
font-weight: 400;
font-size: 30px;
line-height: 82.7%;
letter-spacing: -0.145em;
text-transform: capitalize;
color:var(--text);
margin:15px -10px;
}
.container p{
width: 96px;
height: 24px;
font-family: 'Overpass';
font-style: normal;
font-weight: 700;
font-size: 19px;
line-height: 124.2%;
text-transform: capitalize;
color: var(--text);
margin:30px -24px;
}



.Review{
width: 384px;
height: 144px;
font-family: 'Overpass';
font-style: normal;
font-weight: 400;
font-size: 19px;
line-height: 124.2%;
text-transform: capitalize;
color: var(--text);
}


.buttonContainer{
    display: flex;
    justify-content: end;
    margin-bottom: 80px;
    align-items: center;
    text-align: center;
    
   
}
.buttonContainer button {
    font-size: 1rem;
    font-weight: 600;
    background-color: var(--counter);
    border: none;
    color: var(--border);
    height: 40px;
    width: 140px;
    border-radius: 4px;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 4px 12px -2px,
      rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
    margin-left: 5px;
  }

  .smallButton {
   
    width: 30px !important;
   
    font-size: 1.6rem !important;
    font-weight: bold;
    line-height: 30px;
    
    
  }
  .smallButton img {
    width: 20px;
   margin-left: 5px;
   
    
  }

  .counter {
    display: inline-block;
    
    width: 20px;
    margin-left: 5px;
    font-size: 1.2rem;
    font-weight: 700;
    
    color: var(--border);
    font-family: 'Overpass';
  }

